 .homepage-container {
position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  z-index: 2000; /* Ensure it appears above the header */
  text-align: right;
  padding-top: 2vh;
  overflow: scroll;
}




.home-title{
    font-family: "karantina";
    font-size: 3em;
    font-weight: 400;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    width: 90vw;
    color: #1bbfe5;
    margin-top: 13vh;
    text-align: center;

}

